/* Globalne ustawienia */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Kontener całej aplikacji */
.App {
  position: relative;
  height: 100vh;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f0f0f0;
}

/* Kontener mapy z zoomem */
.map-container {
  position: relative; /* Kompas będzie absolutnie pozycjonowany względem tego kontenera */
  width: 100%;
  height: 100%;
  overflow: hidden;
}

/* Kompas w prawym górnym rogu widocznej części mapy */
.compass-container {
  position: sticky;
  top: 20px;
  right: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 10;
  /* Utrzymywanie kompasu w prawym górnym rogu */
}

.compass-icon {
  width: 60px; /* Stały rozmiar kompasu */
  height: 60px;
  transition: transform 0.1s ease-in-out;
}

.north-arrow {
  position: absolute;
  top: 45px;
  width: 10px; /* Rozmiar strzałki północnej */
  height: 10px;
  background-color: red;
  clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
  z-index: 15;
}

.azimuth-value {
  font-size: 14px;
  margin-top: 5px;
  font-weight: bold;
  color: #333;
}

/* Tło mapy */
.background {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.background-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Przyciski do zoomowania */
.zoom-controls {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 10px;
  z-index: 10;
}

.zoom-controls button {
  padding: 10px 15px;
  font-size: 18px;
  background-color: #333;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.zoom-controls button:hover {
  background-color: #555;
}
